.myModal {
  position: fixed;
  z-index: 9999;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: none;
  background: rgb(0, 0, 0, 0.5);
}
.myModalContent {
  padding: 25px;
  background: white;
  border-radius: 16px;
  width: 50%;
}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
